.our-locations {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background-image: url("../img/backgroumd-line-locations.png");
    background-repeat: no-repeat;
    opacity: 0.2;
    transform: rotate(-0.11deg);
  }

  & .heading {
    margin-bottom: 34px;
  }
}
